body {
  background: #00bdca;
  line-height: 1.4;
  font-family: "Montserrat", sans-serif;
}

image {
  max-width: 100%;
  height: auto;
}

a {
  text-decoration: none;
  color: inherit;
}

.container-inner {
  width: 970px;
  margin: 0 auto;
}

.white-rounded-container {
  width: 1170px;
  margin: 0 auto;
  background-color: #fff;
  background-color-box-shadow: 0px 24px 72px 0 rgba(29, 53, 78, 0.25);
  border-radius: 16px;
}

.btn {
  display: inline-block;
  border-radius: 32px;
  height: 64px;
  padding: 0 65px;
  line-height: 64px;
}

.btn-dark {
  background-color: #1d354e;
}

.block {
  display: flex;
}

.header {
  position: relative;
  padding: 66px 0 117px;
  z-index: 5;
}

.top {
  height: 100%;
  display: flex;
  align-items: center;
}

.top__nav {
  margin-left: auto;
}

.logo__image {
  display: inline-block;
  margin-right: 12px;
}

.logo__text {
  display: inline-block;
  color: #fff;
  vertical-align: top;
}

.logo__upper-text {
  font-size: 24px;
  font-weight: 600;
  line-height: 1.15;
}

.logo__upper-text--coloured {
  color: #56ecf6;
}

.logo__bottom-text {
  font-size: 13px;
  font-weight: 500;
}

.nav {
  display: flex;
  align-items: center;
}

.nav__item {
  margin: 0 18px;
}

.nav__link {
  display: block;
  font-size: 16px;
  font-weight: 600;
  color: #fff;
}

.nav__btn {
  padding: 13px 30px;
  border-radius: 16px;
  color: #1d354e;
  background-color: #ffffff;
}

.js-dropdown {
  display: none;
}

.hero {
  position: relative;
  background-position: 0 0;
  background-repeat: no-repeat;
  z-index: 150;
}

.hero__text {
  margin-left: 45%;
  text-align: center;
  width: 30%;
  color: #ffffff;
  font-weight: 600;
}

.hero__title {
  font-size: 72px;
  line-height: 1.17;
}

.hero__descr {
  margin-bottom: 30px;
  font-size: 30px;
  line-height: 1.6;
}

.hero__tv-wrapper {
  position: relative;
  z-index: -1;
  margin-top: 209px;
  height: 720px;
}

.hero__robot {
  position: absolute;
  top: -759px;
  left: -100px;
}

.hero__cube-light-shaded {
  position: absolute;
  right: -15%;
  top: -25%;
}

.hero__tv {
  position: absolute;
  top: 48px;
  left: 48px;
  bottom: 48px;
  right: 48px;
  background-color: #000;
  border-radius: 16px;
}

.forecast {
  position: relative;
  background-color: #1d354e;
  z-index: 10;
  padding: 144px 0 144px;
}

.forecast__container {
  position: relative;
  z-index: 5;
}

.forecast__image {
  position: relative;
  z-index: 5;
}

.forecast__image img {
  width: 100%;
}

.forecast:before {
  position: absolute;
  content: '';
  top: 0;
  right: 0;
  left: 0;
  width: 100%;
  height: 200%;
  background-color: #1d354e;
  z-index: 10;
  transform: skewY(30deg);
  transform-origin: top right;
  z-index: 2;
}

.forecast:after {
  position: absolute;
  content: '';
  bottom: 0;
  right: 0;
  left: 0;
  width: 100%;
  height: 200%;
  background-color: #1d354e;
  z-index: 10;
  transform: skewY(30deg);
  transform-origin: bottom left;
  z-index: 2;
}

.forecast .forecast__cube-top {
  position: absolute;
  top: calc(-1 * (56vw + 149px));
  left: -20vw;
  z-index: 10;
}

.howitworks {
  position: relative;
  z-index: 10;
}

.howitworks__container {
  padding: 88px 100px;
}

.howitworks__wrapper {
  display: flex;
}

.howitworks__column {
  flex-basis: 45%;
}

.work {
  display: flex;
  flex-direction: column;
}

.news__wrapper {
  display: flex;
}

.single-news {
  flex-grow: 1;
}

.faq__container {
  padding: 88px 100px 134px;
}

.answer-hidden {
  display: none;
}
